@import '../../../global.less';

.questContainer {
  text-align: center;
  flex: none;
  .questInfo {
    margin: 1em 0;
  }
  .tableActions {
    margin: 8px;
    text-align: left;
  }
}

.enemyRowWithChange {
  border-left: 4px solid #4c84ff;
  cursor: pointer;
  &.rowShow {
    background: rgb(240, 247, 255);
  }
}

@border-color-base: #e5e6eb;@layout-body-background: #f5f6fa;@primary-color: #4c84ff;@table-selected-row-bg: #f5f6fa;